














































































import {Component, Vue} from "vue-property-decorator";
import {GetAllbackLogsDocument, StockBackLog} from "~/gql";
import {mapState} from "vuex";

@Component({
    layout: 'console',
    computed: {
        ...mapState({
            admin: (store: any) => store.admin.administrator,
            vendor: (store: any) => store.admin.vendor,
            vendorStore: (store: any) => store.admin.vendorStore,
        }),
    },
    apollo: {
        stockBackLogs: {
            query: GetAllbackLogsDocument,
            variables() {
                if (!this.vendorStore) {
                    return {
                        limit: this.limit,
                        offset: this.offset
                    }
                } else {
                    return {
                        limit: this.limit,
                        offset: this.offset,
                        store: this.vendorStore.id
                    }
                }
            }
        }
    }
})
export default class StockBackorders extends Vue {
    private limit = 50
    private offset = 0

    private stockBackLogs: StockBackLog[]

    // Table
    private gridOptions: any = {};
    private gridApi: any = null;
    private defaultColDef = {
        sortable: true,
        editable: false,
        resizable: true,
        suppressMenu: true
    };
    private columnDefs = [
        {
            headerName: 'Variant',
            filter: false,
            field: 'variant.variant.name'
        },
        {
            headerName: 'Quantity',
            filter: false,
            field: 'quantity'
        },
        {
            headerName: 'Store',
            filter: false,
            field: 'store.storeName'
        },
    ]

    onGridReady() {
        this.gridApi = this.gridOptions!.api;
        this.gridApi!.sizeColumnsToFit();
    }

}
